import { StatusAuthorizedType } from 'redux/appSlice';
import { DialogsType } from 'redux/dialogsReducer';
import { PostsType } from 'redux/profileSlice';
import { AppStoreType } from 'redux/store';
import {
  AuthorizedUserType,
  ContactsType,
  MessageType,
  Nullable,
  UserProfileType,
  UserType,
} from 'type';

export const getAuthorizedStatus = (state: AppStoreType): StatusAuthorizedType =>
  state.app.authorizedStatus;

export const getAuthorizedUser = (state: AppStoreType): AuthorizedUserType =>
  state.app.authorizedUser;

export const selectServerError = (state: AppStoreType): Nullable<string> =>
  state.app.serverError;

export const selectPhotosSmall = (state: AppStoreType): Nullable<string> =>
  state.app.authorizedProfileUser.photos.small;

export const selectMenuIsShow = (state: AppStoreType): boolean =>
  state.headerPage.menuIsShow;

export const selectCaptchaUrl = (state: AppStoreType): Nullable<string> =>
  state.app.captchaUrl;

export const selectContacts = (state: AppStoreType): ContactsType =>
  state.profilePage.userProfile.contacts;

export const selectDialogs = (state: AppStoreType): DialogsType[] =>
  state.dialogsPage.dialogs;

export const selectMessages = (state: AppStoreType): MessageType[] =>
  state.dialogsPage.messages;

export const selectIsLoading = (state: AppStoreType): boolean => state.app.isLoading;

export const selectPosts = (state: AppStoreType): PostsType[] => state.profilePage.posts;

export const selectUserProfile = (state: AppStoreType): UserProfileType =>
  state.profilePage.userProfile;

export const selectUserStatus = (state: AppStoreType): Nullable<string> =>
  state.profilePage.userStatus;

export const selectUsers = (state: AppStoreType): UserType[] => state.usersPage.items;

export const selectTotalCount = (state: AppStoreType): number =>
  state.usersPage.totalCount;

export const selectPageSize = (state: AppStoreType): number => state.usersPage.pageSize;

export const selectCurrentPage = (state: AppStoreType): number =>
  state.usersPage.currentPage;

export const selectPortionsNumber = (state: AppStoreType): number =>
  state.usersPage.portionsNumber;

export const selectUserSubscription = (state: AppStoreType): number[] =>
  state.usersPage.userSubscription;
